	import pickRegions from '@/components/mpvue-citypicker/mpvueCityPicker.vue'
    import util from '../../common/util.js'
	export default{
		components: {
			pickRegions
		},
		onLoad() {
			uni.getStorage({
				key: 'deliAddress',
				success: (e) => {
					this.addressData = e.data;
				}
			})
		},
		data() {
			return {
				genderIndex:0,
				addressData:{
					name:'',
					mobile:'',
					regions:'',
                    provinceName:'',
                    cityName:'',
                    areaName:'',
					detailAddress:''
				}
			}
		},
		methods: {
			formSubmit(){
				let data = this.addressData;
				if(!data.name){
					uni.showToast({
						title:'请填写收货人姓名',
						duration:1000,
						icon:'none'
					});
				}
				if(!util.verifyMobile(data.mobile) ){
					uni.showToast({
						title:'请输入正确的手机号码',
						duration:1000,
						icon:'none'
					});
					return;
				}
				if(!data.regions){
					uni.showToast({
						title:'请在选择所在地区',
						duration:1000,
						icon:'none'
					});
					return;
				}
				if(!data.detailAddress){
					uni.showToast({
						title:'请填写门牌号信息',
						duration:1000,
						icon:'none'
					});
					return;
				}
				let pages = getCurrentPages()
				let prePage = pages[pages.length - 2]
				prePage.setData({
					addressData: data
				})
				uni.showToast({
					title:'保存成功',
					duration:1000,
					icon:'none'
				});
				uni.setStorage({
					key:'deliAddress',
					data: this.addressData,
					success:()=> {
						uni.navigateBack()
					}
				})
			},
			showRegionsPicker(){
				this.$refs['regions'].showPickerView()
			},
			// 获取选择的地区
			confirm(value){
				console.log(value.label.split('-'));
                this.addressData.provinceName = value.label.split('-')[0],
                this.addressData.cityName = value.label.split('-')[1],
                this.addressData.areaName = value.label.split('-')[2],
				this.addressData.regions = value.label.split('-').join('')
			},
			cancel(value){
				console.log(value);
			}
		}
	}
